<template>
  <div class="review-manage-container">
    <div class="box-container">
      <div class="add-new-seller">
        <el-button type="primary" size="small" style="display:none;margin-bottom:15px "  @click="addNewSeller()"></el-button>
      </div>
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-bottom: 0">
        <el-form :inline="true" :model="filters" label-width="80px">
          <el-form-item :size="'small'" label="">
            <el-select
                v-model="filters.authStatus"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in authStatusList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="" style="display:none">
            <el-select
                v-model="filters.commercial"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in commercialList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="" style="display:none">
            <el-select
                v-model="filters.district"
                placeholder=""
                style="width: 200px">
              <el-option
                  v-for="item in districtList"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-date-picker
                v-model="filters.time"
                type="datetimerange"
                placeholder=""
                value-format="yyyy-MM-dd HH:mm"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                style="width: 350px">
            </el-date-picker>
          </el-form-item>
          <el-form-item :size="'small'" label="">
            <el-input placeholder="" v-model="filters.keywords" class="input-with-select" style="width: 300px">
              <el-select v-model="filters.searchType" slot="prepend" @change="inputWithSelectChange" placeholder="" style="width: 120px">
                <el-option label="" value="1" style="display:none"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4" style="display:none"></el-option>
                <el-option label="" value="5" style="display:none"></el-option>
              </el-select>
            </el-input>
          </el-form-item>
          
          <div></div>
          <el-form-item size="small" label="" prop="addrProvince">
            <el-select
                v-model="filters.addrProvince"
                placeholder=""
                @change="selectBrand1"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl1List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrCity">
            <el-select
                v-model="filters.addrCity"
                placeholder=""
                @change="selectBrand2"
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl2List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="" prop="addrArea">
            <el-select
                v-model="filters.addrArea "
                placeholder=""
                style="width: 160px">
              <el-option
                  v-for="item in cateLvl3List"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <div></div>
          <el-form-item class="mr0" :size="'small'">
            <el-button type="primary" size="small" @click="commitForm"></el-button>
            <el-button size="small" class="important" @click="getStatus"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </div>

    <div class="box-container">
      <!---->
      <el-col class="crm-table-wrap" v-loading="listLoading" :span="24">
        <el-table
            :data="newList"
            @selection-change="handleSelectionChange"
            style="width: 100%">
          <el-table-column
              type="selection"
              width="55">
          </el-table-column>
          <el-table-column
              type="index"
              label=""
              width="50">
          </el-table-column>
          <el-table-column
              prop="licenceNo"
              label="	"
              width="200">
          </el-table-column>
          <el-table-column
              prop="shopName"
              label=""
              width="120">
          </el-table-column>
          <!-- 
          <el-table-column
              prop="ownerName"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="district"
              label=""
              width="120">
            <template slot-scope="scope">
              <span>{{ scope.row.district == "1" ? "":""}}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="commercial"
              label=""
              width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.commercial == '1'">{{ "" }}</span>
              <span v-else-if="scope.row.commercial == '2'">{{ "" }}</span>
              <span v-else-if="scope.row.commercial == '3'">{{ "" }}</span>
              <span v-else-if="scope.row.commercial == '4'">{{ "" }}</span>
              <span v-else-if="scope.row.commercial == '5'">{{ "" }}</span>
              <span v-else-if="scope.row.commercial == '6'">{{ "" }}</span>
              <span v-else-if="scope.row.commercial == '7'">{{ "" }}</span>
            </template>
          </el-table-column>
          -->
          <el-table-column
              prop="ownerName"
              label=""
              width="120">
          </el-table-column>
          <el-table-column
              prop="phoneNo"
              label=""
              width="120">
          </el-table-column>
          <!--
          <el-table-column
              prop="salesManNames"
              label="">
          </el-table-column>
          -->
          <el-table-column
              prop="applyTime"
              label=""
              width="200">
            <template slot-scope="scope">
              <span>{{ new Date(scope.row.applyTime).Format('yyyy-MM-dd hh:mm:ss')}}</span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span>{{scope.row.addrProvinceName}}</span><span v-if="scope.row.addrProvinceName!=scope.row.addrCityName">{{scope.row.addrCityName}}</span><span>{{scope.row.addrAreaName}}</span><span>{{scope.row.addrDetail}}</span>
            </template>
          </el-table-column>
          <el-table-column
              prop="authStatus"
              label="">
            <template slot-scope="scope">
              <span v-if="scope.row.authStatus == '1'">{{ "" }}</span>
              <span v-else-if="scope.row.authStatus == '2'">{{ "" }}</span>
              <span v-else-if="scope.row.authStatus == '3'">{{ "" }}</span>
              <span v-else-if="scope.row.authStatus == '4'">{{ "" }}</span>
            </template>
          </el-table-column>
          <el-table-column
              fixed="right"
              label=""
              width="240"
          >
            <template slot-scope="scope" >
              <el-button
                  type="text"
                  size="mini"
                  @click="sellerDetail(scope.row.sellerId)"
              ></el-button>
              <el-button
                  type="text"
                  size="mini"
                  @click="disapprovalDialog(scope.row.sellerId)"
              ></el-button>
              <el-button
                  type="text"
                  size="mini"
                  @click="approval(scope.row.sellerId)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>

      <!---->
      <el-col :span="24" class="footer-toolbar">
        <el-button
            type="primary"
            size="small"
            :disabled="batchPass"
            @click="passBatch(ids)"
        ></el-button>
        <!--<el-button type="primary" class="btn-jump-to" @click="jumpTo"></el-button>-->
        <el-pagination
            background
            layout="total,prev, pager, next,jumper"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="pageSize"
            :total="total"
            style="float:right;">
        </el-pagination>
      </el-col>
    </div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="cancelDialog">
      <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="failReason">
      </el-input>
      <div style="text-align: center;margin-top: 30px">
        <el-button size="small" @click="cancelDialog"> </el-button>
        <el-button size="small" type="primary" @click="disapproval"> </el-button>
      </div>

    </el-dialog>


  </div>
</template>

<script>
  export default {
    name: "Reviewmanage",
    // beforeRouteLeave(to, from, next) {
    //   // console.log(to.path)
    //   if (to.path !== '/seller/review/addSeller') {
    //     next()
    //   }else {
    //     next("/seller/review/")
    //   }
    // },
    data(){
      return{
        listLoading:false,

        //
        authStatusList:[
          {
            code:"1",
            name:""
          },
          {
            code:"3",
            name:""
          },
        ],
        commercialList:[
          {
            code:"",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
          {
            code:"3",
            name:""
          },
          {
            code:"4",
            name:""
          },
          {
            code:"5",
            name:""
          },
          {
            code:"6",
            name:""
          },
          {
            code:"7",
            name:""
          },
        ],
        districtList:[
          {
            code:"",
            name:""
          },
          {
            code:"1",
            name:""
          },
          {
            code:"2",
            name:""
          },
        ],

        //
        cateLvl1List:[],
        cateLvl2List:[],
        cateLvl3List:[],

        filters: {
          //1-2-3-4-
          authStatus :'1',
          // : 1-2-3-4-5-6-7-
          commercial:'',
          //: 1-2-
          district:'',
          //  1-2-3-4-5-
          searchType:'',
          //
          keywords:'',
          //
          // appStartTime:'',
          // //
          // appEndTime:'',
          time:[],

          //
          addrProvince:'',
          //
          addrCity:'',
          //
          addrArea:'',



        },
        //
        newList:[],

        //
        pageSize:10,

        pageNo: 1,
        //
        currentPage:1,
        //
        total:0,

        //
        batchPass : true,
        ids:'',

        //
        dialogVisible:false,
        failReason:'',
        sellerIds:''
      }

    },
    created(){
      this.getListNewList()
      this.getOneCategory()
    },
    methods:{
      addNewSeller(){
        this.$router.push({
          'path':'/seller/review/addSeller'
        })
      },
      //1
      getOneCategory(){
        this.$request.post('/lsh/seller-manager/region/province',{}, true, (res) => {
          this.cateLvl1List = [...res]
        })
      },
      selectBrand1(){
        this.filters.addrCity = ''
        this.cateLvl2List = []
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getTwoCategory()
      },
      //2
      getTwoCategory(){
        if(this.filters.addrProvince  == '')return
        this.$request.post('/lsh/seller-manager/region/newCity',{parentCode:this.filters.addrProvince}, true, (res) => {
          this.cateLvl2List = [...res]
        })
      },
      selectBrand2(){
        this.filters.addrArea = ''
        this.cateLvl3List = []
        this.getThreeCategory()
      },
      //3
      getThreeCategory(){
        if(this.filters.addrCity == '')return
        this.$request.post('/lsh/seller-manager/region/newDistrict',{parentCode:this.filters.addrCity}, true, (res) => {
          this.cateLvl3List = [...res]
        })
      },
      //
      getListNewList() {
        let params = {
          //
          authStatus:this.filters.authStatus,
          //
          commercial:this.filters.commercial,
          //
          district:this.filters.district,

          searchType:this.filters.searchType,
          //
          // keywords:this.filters.keywords,

          //
          addrProvince: this.filters.addrProvince,
          addrCity: this.filters.addrCity,
          addrArea: this.filters.addrArea,
          //
          appStartTime: this.filters.time?this.filters.time[0]?this.filters.time[0]:'':'',
          appEndTime: this.filters.time?this.filters.time[1]?this.filters.time[1]:'':'',
          //
          sortType: 1,
          sortValue: 1,

          pageNo: this.pageNo,
          pageSize: 10,

        };
        // 
        if(this.filters.searchType == '3') {
          params.licenceNo = this.filters.keywords || ''; // 
        } else if(this.filters.searchType == '1') {
          params.ownerName = this.filters.keywords || ''; // 
        } else if(this.filters.searchType == '2') {
          params.phoneNo = this.filters.keywords || ''; // 
        } else if(this.filters.searchType == '4') {
          params.shopName = this.filters.keywords || ''; // 
        } else if(this.filters.searchType == '5') {
          params.salesmanName = this.filters.keywords || ''; // 
        }
        this.postSearch(params)
      },
      postSearch(params) {
        // this.listLoading = true;
        this.$request.post('/lsh/seller-manager/seller/select/newList', params, false, (res) => {

            console.log(res.data)
            // this.listLoading = false;
            this.newList = res.list
            this.total = res.page.count
            this.pageNo = res.page.pageNo

        })
      },
      //
      commitForm() {
        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      //
      getStatus() {
        //1-2-3-4-
        this.filters.authStatus = '1',
        // : 1-2-3-4-5-6-7-
        this.filters.commercial = '',
        //: 1-2-
        this.filters.district = '',
        //  1-2-3-4-5-
        this.filters.searchType = '',
        //
        this.filters.keywords = '',

        this.filters.time = [];
        //
        this.filters.appStartTime = '',
        //
        this.filters.appEndTime = '',

        //
        this.filters.addrProvince = '',
        //
        this.filters.addrCity = '',
        //
        this.filters.addrArea = '',

        this.pageNo = 1
        this.currentPage = 1
        this.getListNewList()
      },
      inputWithSelectChange(val){
        this.filters.keywords = ""
      },
      handleSelectionChange(val) {
        let arr = val
        console.log(arr)
        let idsArr = []
        if(arr.length > 0){
          this.batchPass = false
          arr.map((e,i) => {
            idsArr.push(e.sellerId)
          })
          this.ids = idsArr.join(",")
        }else{
          this.ids = ""
          this.batchPass = true
        }
      },
      //
      approval(sellerIds){
        let params = {sellerIds,authResult:1}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/approvalBatch',params,false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
          })
        })
      },
      //
      disapprovalDialog(sellerIds){
        this.dialogVisible = true
        this.sellerIds = sellerIds
      },
      disapproval(){
        let params = {sellerIds:this.sellerIds,authResult:2,failReason:this.failReason}
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/approvalBatch',params,false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
              this.dialogVisible = false
              this.failReason = ""
              this.getListNewList()
            }
          })
        })
      },
      cancelDialog(){
        this.dialogVisible = false
        this.failReason = ""
      },
      //
      passBatch(){
        this.$confirm(``, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$request.post('/lsh/seller-manager/seller/approvalBatch',{sellerIds:this.ids,authResult:1},false,res => {
            if(res.ok){
              this.$message({
                message: '',
                type: 'success'
              });
              this.getListNewList()
            }else{
              this.$message({
                message: res.message,
                type: 'warning'
              });
            }
          })
        })
      },
      sellerDetail(sellerId){
        this.$router.push({
          path:'/seller/review/reviewDetail?sellerId='+sellerId
        })
      },
      //
      jumpTo() {
        this.$emit('current-change')
      },
      //
      handleCurrentChange(val) {
        this.pageNo = val
        this.currentPage = val
        this.getListNewList()
      },
    }
  }
</script>

<style lang="scss" scoped>
  .review-manage-container{
  .box-container{
    background-color: #fff;
    padding: 15px;
    margin-bottom: 30px;
    add-new-seller{
      margin-bottom: 30px;
    }
  &:after{
     content: '';
     display: block;
     width: 0;
     height: 0;
     clear: both;
   }
  }
  }
  .el-form-item {
    margin-bottom: 20px;
  }
</style>
<style >
  .review-manage-container .el-table{text-align: center}
  .review-manage-container .el-table th{text-align: center}
  .review-manage-container .el-table td{text-align: center}
  .review-manage-container .mini .el-form-item__label {width: 40px!important;}
</style>


